export default {
	data() {
		return {
			shopId: 0
		}
	},
	onLoad(options) {
		this.shopId = options.shopId
	},
	methods: {
		addMaster(e) {
			let that = this
			uni.showModal({
				title: '是否要添加该师傅？',
				success(confirm) {
					if (confirm.confirm) {
						let phone = e.detail.value.phone
						that.$api.apiPost({
							url: '/api/masterManage/add',
							data: {
								phone: phone,
								shopId: that.shopId
							}
						})
						.then((res) => {
							uni.navigateBack()
							uni.showToast({
								icon: 'none',
								title: '添加维修师傅成功'
							})
						})
					}
				}
			})
		}
	}
}
